import { FC } from "react";
import classNames from "classnames/bind";

import { Container } from "common/components/Container";
import {
    Heading,
    HeadingType,
    HeadingTag,
} from "common/components/new/Heading";
import { PricePerMonth } from "common/components/new/PricePerMonth";
import { Button, ButtonSize } from "common/components/new/Button";
import { Picture } from "common/components/Picture";
import { HeaderShort } from "common/components/new/HeaderShort";
import { Theme } from "common/constants/enum";

import { SmallBanner } from "../SmallBanner";
import { HeadProps } from "./Head.types";
import styles from "./Head.module.scss";

const cx = classNames.bind(styles);

export const Head: FC<HeadProps> = ({
    titles,
    price,
    images,
    subtitle,
    contacts,
    buttonText,
    smallBanner,
    logoTheme,
    theme = Theme.DARK,
    onScrollToAppointment,
    isFixedButtonActive,
}) => {
    return (
        <section className={cx("root")}>
            <SmallBanner text={smallBanner} onClick={onScrollToAppointment} />
            <HeaderShort
                {...contacts}
                button={{
                    name: buttonText,
                    onClick: onScrollToAppointment,
                }}
                theme={theme}
                isFixedButtonActive={isFixedButtonActive}
                logoTheme={logoTheme}
                className={cx("header")}
            />
            <Container>
                <Heading
                    className={cx("title")}
                    tag={HeadingTag.H1}
                    type={HeadingType.XL}
                >
                    {titles.map((title) => (
                        <span className={cx("title__text")} key={title}>
                            {title}
                        </span>
                    ))}
                </Heading>
                <div className={cx("container")}>
                    <div className={cx("container-images")}>
                        <PricePerMonth
                            {...price}
                            value={price.value.replace(/\s/g, "")}
                            isRound={true}
                            className={cx("price-per-month")}
                        />
                        {images.map((image, index) => (
                            <Picture
                                classNameImg={cx("image", `image_${index + 1}`)}
                                width={257}
                                height={347}
                                key={index}
                                {...image}
                            />
                        ))}
                    </div>
                    <div className={cx("container-text")}>
                        <div className={cx("title-mobile")}>
                            {titles.map((title) => (
                                <div key={title} className={cx("title__text")}>
                                    <Heading
                                        className={cx("title__first-text")}
                                    >
                                        {title}
                                    </Heading>
                                    <Heading
                                        className={cx(
                                            "subtitle",
                                            "subtitle_mobile"
                                        )}
                                        type={HeadingType.M}
                                    >
                                        {subtitle}
                                    </Heading>
                                </div>
                            ))}
                        </div>
                        <Heading
                            className={cx("subtitle")}
                            type={HeadingType.M}
                        >
                            {subtitle}
                        </Heading>
                        <Button
                            size={ButtonSize.M}
                            className={cx("button")}
                            onClick={onScrollToAppointment}
                        >
                            {buttonText}
                        </Button>
                    </div>
                </div>
            </Container>
        </section>
    );
};
