import { FC } from "react";
import classNames from "classnames/bind";

import { Heading } from "common/components/new/Heading";
import { Picture } from "common/components/Picture";
import { getAnalyticsId } from "common/utils/AnalyticsId";
import { Button, ButtonSize, ButtonTheme } from "common/components/new/Button";

import { CheckUpList } from "./components/CheckUpList";
