import { FC } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Text } from "common/components/Text";

import { BenefitProps, BenefitsCardProps } from "./BenefitsCard.types";
import styles from "./BenefitsCard.module.scss";

const cx = classNames.bind(styles);

export const BenefitsCard: FC<BenefitsCardProps> = ({
    title,
    list,
    className,
}) => {
    const {
        benefit1,
        benefit2,
        benefit3,
        benefit4,
        benefit5,
        benefit6,
        benefit7,
    } = list;

    const getStyledBenefits = (benefits: BenefitProps[]) => {
        return benefits.map((benefit) => {
            return (
                <li key={benefit.header} className={cx("benefit-item")}>
                    <Heading
                        tag={HeadingTag.H1}
                        className={cx("benefit-item-header")}
                    >
                        {benefit.header}
                    </Heading>
                    <Text className={cx("benefit-item-description")}>
                        {benefit.description}
                    </Text>
                </li>
            );
        });
    };

    return (
        <div className={cx(className, "root")}>
            <Heading
                type={HeadingType.XL}
                tag={HeadingTag.H2}
                className={cx("title")}
            >
                {title}
            </Heading>
            <div className={cx("benefits-flex-wrapper")}>
                <div className={cx("benefits-group")}>
                    <ul className={cx("benefits-list")}>
                        {getStyledBenefits([benefit1, benefit2])}
                    </ul>
                    <ul className={cx("benefits-list")}>
                        {getStyledBenefits([benefit3, benefit4])}
                    </ul>
                </div>
                <div className={cx("benefits-group")}>
                    <ul className={cx("benefits-list")}>
                        {getStyledBenefits([benefit5, benefit6])}
                    </ul>
                    <ul className={cx("benefits-list")}>
                        {getStyledBenefits([benefit7])}
                    </ul>
                </div>
            </div>
        </div>
    );
};
